.buttonWrap {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 0 30px 0 0;
}

.button {
    background-color: #212121;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 40px;
    cursor: pointer;
    border: none;
}

.buttonDisabled{
    background-color: #f5f5f5;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 40px;
    border: none;
}

.button:hover {
    background-color: #424242;
    transition: 0.5s;
}

.button:active {
    background-color: #616161;
}